import { Jadwal_tipe } from 'src/app/sdk/hr/models/jadwal-_tipe';
import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiService, DirektoriPegawaiStatusService, PayrollPtkpService, AbsensiJadwalTipeService, GaKlaimKategoriService, DirektoriPegawaiTypeService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Pegawai, Pegawai_status, Ptkp, Klaim_kategori, Pegawai_type } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private direktoriPegawaiService: DirektoriPegawaiService,
        private direktoriPegawaiStatusService: DirektoriPegawaiStatusService,
        private direktoriPegawaiTypeService: DirektoriPegawaiTypeService,
        private payrollPtkpService: PayrollPtkpService,
        private gaKlaimKategoriService: GaKlaimKategoriService,
        private absensiJadwalTipeService: AbsensiJadwalTipeService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private activatedRoute: ActivatedRoute,
        private router: Router,
    ) { }

    id_pegawai: string = ''
    dataPegawai: Pegawai = {}
    listStatus: Pegawai_status[] = []
    listPTKP: Ptkp[] = []
    listType: Pegawai_type[] = []
    listJadwalTipe: Jadwal_tipe[] = []
    listKlaimKategori: Klaim_kategori[] = []

    ngOnInit() {
        this.id_pegawai = this.activatedRoute.snapshot.paramMap.get('id_pegawai')
        this.getPreeData()
        this.form()
        this.getData()

    }

    getPreeData() {
        this.direktoriPegawaiStatusService.getDirektoriPegawaiStatuses().subscribe(
            data => {
                this.listStatus = data.data
            }
        )
        this.absensiJadwalTipeService.getAbsensiJadwalTipes().subscribe(
            data => {
                this.listJadwalTipe = data.data
            }
        )
        this.payrollPtkpService.getPayrollPtkps().subscribe(
            data => {
                this.listPTKP = data.data
            }
        )
        this.gaKlaimKategoriService.getGaKlaimKategoris().subscribe(
            data => {
                this.listKlaimKategori = data.data
            }
        )
        this.direktoriPegawaiTypeService.getDirektoriPegawaiTypes().subscribe(
            data => {
                this.listType = data.data
            }
        )
    }

    getData() {
        this.spinner.show()
        this.direktoriPegawaiService.getDirektoriPegawaisId(this.id_pegawai).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()
            }
        )
    }

    dataForm: FormGroupTypeSafe<Pegawai>
    form() {
        this.dataForm = this.fb.group<Pegawai>(
            {
                no_induk: new FormControl('', [Validators.required]),
                no_absensi: new FormControl(''),
                nama_belakang: new FormControl(''),
                nama_depan: new FormControl(''),
                jenis_kelamin: new FormControl(''),
                kewarganegaraan: new FormControl(''),
                type_pegawai: new FormControl(''),
                tanggal_masuk: new FormControl(''),
                hp: new FormControl(''),
                id_ptkp: new FormControl(''),
                id_klaim_kategori: new FormControl(''),
                id_jadwal_tipe: new FormControl(''),
                email: new FormControl(''),
                status_pegawai: new FormControl(''),
                id_pegawai: new FormControl(''),
                id_user_create: new FormControl(''),
                bpjs_kesehatan: new FormControl(''),
                bpjs_jp: new FormControl(''),
                bpjs_jkm: new FormControl(''),
                bpjs_jkk: new FormControl(''),
                bpjs_jht: new FormControl(''),
                agama: new FormControl(''),
                tanggal_lahir: new FormControl(''),
                tempat_lahir: new FormControl(''),
                hp2: new FormControl(''),
                status_perkawinan: new FormControl(''),
                phone: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                tanggal_keluar: new FormControl(''),
                foto: new FormControl('')
            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataPegawai = this.dataForm.value
        this.direktoriPegawaiService
            .putDirektoriPegawaisId({ id: this.id_pegawai, body: this.dataPegawai })
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai/list/all'])
    }

}
