import { Component, OnInit } from '@angular/core';
import { DirektoriPegawaiService } from 'src/app/sdk/hr/services/direktori-pegawai.service';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPegawaiService: DirektoriPegawaiService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService,
        private activeRoute: ActivatedRoute,
        private router: Router
    ) { }



    type_pegawai = '';
    ngOnInit() {

        this.activeRoute.paramMap.subscribe(
            data => {
                if (data.get('type_pegawai')) {
                    this.type_pegawai = data.get('type_pegawai');
                    this.searchData()
                }
            }
        )
    }

    pageIndex = 1;
    pageSize = 10;
    total = 1;
    listOfData = [];
    loading = true;
    sortValue: string | null = null;
    sortKey: string | null = null;
    filterGender = [{ text: 'male', value: 'male' }, { text: 'female', value: 'female' }];
    search: string = '';
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key;
        this.sortValue = sort.value;
        this.searchData();
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1;
        }
        this.loading = true;
        this.direktoriPegawaiService
            .getDirektoriPegawais(JSON.stringify({
                pageIndex: this.pageIndex, pageSize: this.pageSize, sortKey: this.sortKey, sortValue: this.sortValue, search: this.search, type_pegawai: this.type_pegawai
            }))
            .subscribe((data: any) => {
                this.loading = false;
                this.total = data.data.total;
                this.listOfData = data.data.data;
            });
    }

    updateFilter(value: string): void {
        this.search = value;
        this.searchData(true);
    }




    delete(id) {
        this.spinner.show();
        this.direktoriPegawaiService.deleteDirektoriPegawaisId(id).subscribe(
            data => {
                this.searchData();
                this.spinner.hide();
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel');
    }


}
